<template>
  <div id="links">
    <template v-if="this.linkCount === 0">
      <div class="d-flex justify-content-center align-items-center vh-100">
        <b-spinner type="grow" label="Loading ..." class="spinner--large" />
      </div>
    </template>

    <template v-else>
      <header class="text-center pt-4">
        <img :src="logo" alt="Music By Darcy" height="96" />
        <p class="font-xs pt-1">@musicbydarcy</p>
      </header>

      <div class="container mt-4 mb-5">
        <div class="row justify-content-center">
          <div class="col-md-7 col-lg-5 text-center">
            <ul class="link-list">
              <template v-for="(link, index) in links">
                <li
                  v-if="showLink(link)"
                  :key="index"
                  class="link-list__item"
                  :class="[linkStyle(link.style)]"
                >
                  <a
                    :href="link.websiteUrl"
                    class="link-list__link"
                    @click.prevent="
                      onClick({
                        category: 'Redirect Links',
                        action: 'Link Click',
                        label: link.name,
                        url: link.websiteUrl
                      })
                    "
                  >
                    <b-icon
                      v-if="showIcon(link.category)"
                      :icon="showIcon(link.category)"
                      class="link-list__icon"
                    />
                    {{ link.name }}
                  </a>
                </li>
              </template>
            </ul>
          </div>
        </div>
      </div>

      <footer class="mb-5">
        <ul
          class="list-unstyled d-flex justify-content-center align-items-center"
        >
          <li v-for="(link, name) in social" v-bind:key="name">
            <a
              :href="link"
              class="font-46 mx-3"
              @click.prevent="
                onClick({
                  category: 'Social Profiles',
                  action: 'Link Click',
                  label: name,
                  url: link
                })
              "
            >
              <span class="sr-only">
                Link to {{ name }} social media profile
              </span>
              <i :class="`fab fa-${name} color-grey-darker`"></i>
            </a>
          </li>
        </ul>
      </footer>
    </template>
  </div>
</template>

<script>
import camelcaseKeys from "camelcase-keys";
import base from "@/utilities/airtable";
import logo from "@/assets/favicon.svg";
import config from "@/config";

export default {
  name: "Links",
  data() {
    return {
      logo: logo,
      now: new Date().getTime(),
      records: [],
      social: config.social
    };
  },
  computed: {
    linkCount() {
      return this.links.length;
    },
    links() {
      if (this.records === undefined) return [];
      return this.records.map(record => camelcaseKeys(record.fields));
    }
  },
  mounted() {
    // Load Airtable data
    base(config.airtable.name)
      .select({ view: config.airtable.view })
      .eachPage((records, fetchNextPage) => {
        this.records = records;
        fetchNextPage();
      });
  },
  methods: {
    linkStyle(style) {
      return style === undefined
        ? null
        : `link-list__item--${style.toLowerCase()}`;
    },
    onClick({ category, action, label, url }) {
      // DataLayer Event
      this.$gtm.trackEvent({
        event: "eventTracking",
        category,
        action,
        label,
        value: url
      });

      // Redirect
      window.location = url;
    },
    showIcon(category) {
      if (category.toLowerCase() === "producer") return "music-note-list";
      if (category.toLowerCase() === "podcast") return "mic-fill";
      if (category.toLowerCase() === "youtube") return "tv";
      return false;
    },
    showLink(link) {
      if (link.ready === undefined) return false;
      if (this.timeCheckStart(link.startDate)) return false;
      if (this.timeCheckEnd(link.endDate)) return false;
      return true;
    },
    timeCheckEnd(time) {
      return time !== undefined && this.now > new Date(time).getTime();
    },
    timeCheckStart(time) {
      return time !== undefined && this.now < new Date(time).getTime();
    }
  }
};
</script>
